<template>
  <div>
    <app-header></app-header>
    <div class="miniheader">
      <div class="container">
        <div class="miniheader-logo">
          <router-link to="/">小米官网</router-link>
        </div>
        <div class="miniheader-title">
          <h2>我的购物车</h2>
          <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>
        </div>
        <div class="miniheader-info">
          <i class="iconfont">&#x221a;</i>
          <a href="#" class="user-name">
            <span></span>
          </a>
          <span class="sep">|</span>
          <a href="#" class="info-order">我的订单</a>
        </div>
      </div>
    </div>

    <div class="cartmain">
      <div class="container">
        <div class="cartmain-list">
          <div class="list-head clearfix">
            <div class="col col-check">
              <i
                class="iconfont icon-checkbox"
                :class="{select:isSelectedAll}"
                @click="clickSelectedAll"
              >&#x221a;</i> 全选
            </div>
            <div class="col col-img" style="height:1px"></div>
            <div class="col col-name">商品名称</div>
            <div class="col col-price">单价</div>
            <div class="col col-num">数量</div>
            <div class="col col-total">小计</div>
            <div class="col col-action">操作</div>
          </div>
          <div class="list-body">
            <div
              class="item-box clearfix"
              v-for="(good,index) in cartGoodsList"
              :key="`good_${index}`"
            >
              <div class="col col-check">
                <!-- 不能通过data-来存储一个对象数据？ -->
                <i
                  class="iconfont icon-checkbox"
                  :class="{select:selectGoods_id.includes(good.id)}"
                  @click="clickSelected($event,good)"
                >&#x221a;</i>
              </div>
              <div class="col col-img">
                <a href="#">
                  <img :src="good.img" alt />
                </a>
              </div>
              <div class="col col-name">{{good.name}}</div>
              <div class="col col-price">
                <label class="price">{{good.price}}</label>元
              </div>
              <div class="col col-num">
                <div class="change-num clearfix">
                  <!-- 减少 -->
                  <a data-change="-1" href="javascript:;" @click="changeNum(-1,good)">
                    <i class="iconfont">&#xe60b;</i>
                  </a>
                  <!-- 输入数量 -->
                  <input
                    type="text"
                    v-model="good.quantity"
                    @input="good.quantity=good.quantity.replace(/\D/g, '')"
                    @change="changeNum(Number($event.target.value)-Number(good.quantity),good)"
                  />
                  <!-- 增加 -->
                  <a data-change="1" href="javascript:;" @click="changeNum(1,good)">
                    <i class="iconfont">&#xe609;</i>
                  </a>
                </div>
              </div>
              <div class="col col-total">
                <label class="price">{{good.price*good.quantity}}</label>元
              </div>
              <div class="col col-action">
                <!-- 点击删除此项商品,传当前数量的负数 -->
                <a href="javascript:;" @click="changeNum(-good.quantity,good)">
                  <i class="iconfont">&#xe602;</i>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div class="cartmain-bar clearfix">
          <div class="bar-info">
            <router-link to="/">继续购物</router-link>
            <span class="info-total">
              共
              <i class="total-num">{{totalCartGoods}}</i> 件商品，已选择
              <i class="total-num">{{selectCountNum}}</i> 件
            </span>
          </div>
          <span class="total-price">
            合计：
            <em class="totalprice-num">{{selectToalPrice}}</em>元
          </span>
          <form method="get" action="checkout.html" style="display: inline">
            <input type="hidden" name="ids" id="ids" />
            <a href="javascript:;" id="checkout" class="bar-btn btn-primary" @click="submit">去结算</a>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../utils/api";
import { mapState, mapGetters } from "vuex";

export default {
  created() {
    document.title = "购物车";
    // 获取购物车商品
    // this.$store.dispatch("cart/CHANGEcartGoodsList");
  },

  updated() {},
  data() {
    return {
      //   isSelectedAll是否全选
      isSelectedAll: false,
      selectGoods_id: []
    };
  },
  computed: {
    ...mapState("cart", ["cartGoodsList"]),
    ...mapGetters("cart", ["totalCartGoods"]),
    // selectCountNum已选择多少件
    selectCountNum() {
      return this.selectGoods_id.reduce((sum, item) => {
        return (sum += Number(this.findGoodsInCartGoodsList(item).quantity));
      }, 0);
    },
    // selectToalPrice选中的商品合计多少元
    selectToalPrice() {
      return this.selectGoods_id.reduce((sum, item) => {
        return (sum +=
          Number(this.findGoodsInCartGoodsList(item).quantity) *
          Number(this.findGoodsInCartGoodsList(item).price));
      }, 0);
    }
  },
  watch: {},
  methods: {
    // findGoodsInCartGoodsList用于根据id在购物车数组中查找商品
    findGoodsInCartGoodsList(id) {
      for (var i = 0; i < this.cartGoodsList.length; i++) {
        if (this.cartGoodsList[i].id == id) {
          return this.cartGoodsList[i];
        }
      }
    },
    //   点击单个勾选
    clickSelected($event, good) {
      if (!this.selectGoods_id.includes(good.id)) {
        // 如果点击时，selectGoods_id中没有这个id，说明当前是个没勾选的商品，点击后被勾选
        this.selectGoods_id.push(good.id);
      } else {
        // 如果点击的是已经勾选的，就取消勾选
        const n = this.selectGoods_id.indexOf(good.id);
        this.selectGoods_id.splice(n, 1);
      }

      this.isSelectedAll =
        this.selectGoods_id.length == this.cartGoodsList.length;
      // console.log(this.selectGoods_id);
    },

    // 点击全选
    clickSelectedAll() {
      this.isSelectedAll = !this.isSelectedAll;

      this.selectGoods_id = [];
      if (this.isSelectedAll) {
        // 如果是全选
        for (let i = 0; i < this.cartGoodsList.length; i++) {
          this.selectGoods_id.push(this.cartGoodsList[i].id);
        }
      }
    },

    // 点击增减商品数量
    changeNum(num, good) {
      if (num == -good.quantity) {
        // 如果num==-good.quantity，就是删除
        if (confirm("确认删除此商品！")) {
          // 如果确认删除
          // 如果当前是被勾选的，就从selectGoods_id中先删除它
          if (this.selectGoods_id.includes(good.id)) {
            const n = this.selectGoods_id.indexOf(good.id);
            this.selectGoods_id.splice(n, 1);
          }
          api
            .post("/user/updateCart", {
              id: good.id,
              quantity: Number(good.quantity) + num
            })
            .then(() => {
              // 请求成功后再获取购物车商品，更新购物车
              this.$store.dispatch("cart/CHANGEcartGoodsList");
            });
        }else{
          // 如果输入0，又不确认删除，那么就再请求一次购物车商品，使商品数量回复到原来
          this.$store.dispatch("cart/CHANGEcartGoodsList");
        }
      } else {
        api
          .post("/user/updateCart", {
            id: good.id,
            quantity: Number(good.quantity) + num
          })
          .then(() => {
            // 请求成功后再获取购物车商品，更新购物车
            this.$store.dispatch("cart/CHANGEcartGoodsList");
          });
      }
    },
    // 点击结算
    submit() {
      if (this.selectGoods_id.length == 0) {
        alert("请先勾选商品");
      } else {
        this.$router.push({
          name: "checkout",
          query: { id: JSON.stringify(this.selectGoods_id) }
        });
      }
    }
  }
};
// event.currentTarget指向事件所绑定的元素，而event.target始终指向事件发生时的元素。
</script>

<style lang="scss" scoped>
@import url(../css/iconfont.css);
@import url(../css/style.css);
@import url(../css/cart.css);
</style>

